import { useEffect, useState } from 'react';
import useStore from '../store/index';

const TextTrigger = ({progress, event, triggerPoint = null, text}) => {
  
  const [triggered, setTriggered] = useState(false);

  const setHeaderText = useStore((state) => state.setHeaderText);
  const unsetHeaderText = useStore((state) => state.unsetHeaderText);
  
  useEffect(() => {
    if(triggerPoint !== null) {
      if (progress > triggerPoint && !triggered) {
        setTriggered(true);
        setHeaderText(text);
      }
      else if (progress < triggerPoint && triggered) {
        setTriggered(false);
        unsetHeaderText(text);
      }
    }
    else if (event.type === 'enter' && event.scrollDirection === 'FORWARD'){
      setTriggered(true);
      setHeaderText(text);
    }
    else if (event.type === 'leave' && event.scrollDirection === 'REVERSE') {
      setTriggered(false);
      unsetHeaderText();
    }
  }, [progress, event, setHeaderText])
  return null;
};

export default TextTrigger