/**
 * Layout stuff
 */
const margin = {top: 20, bottom: 20, left: 0, right: 0};

/**
 * For a legend that runs alongside the chart
 * Currently, it is paced underneath
 */
const colorswatchWidth = 10;
const labelColumnWidth = 16;
const legendGutter = 5;
const legendPadding = 16;
const labelGutter = 4;


/**
 * This is being used for the legend. Could be replaced.
 */
const x = d => d.category;

const colorScale = scaleOrdinal({
  range: ['#cea9be', '#e7466d', '#dde44c', '#ff7c53'],
  domain: dataset.map(x), // alternatively uniqueGroups
});

const gridHeight = 18; // in dots
const gridWidth = 10;
  
// sum all counts and determine percentage share for each category
// const getTotal = (data) => data.reduce((acc, curr) => acc + curr.frequency ,0);
// const total = getTotal(data1); // tally counts in data
// const getDotCount = (number, total) => Math.round((number / total) * gridWidth * gridHeight);
// const dotCounts = data1.map((d) => {
//   return {
//     dotCount: getDotCount(d.frequency, total),
//     category: d.letter,
//   }
// });

const ExampleDotMatrix = function({progress, width, height}) {
  /**
   * Layout stuff
   */
  const xMax = width  - margin.left - margin.right;
  const yMax = height - margin.top - margin.bottom;
  const availableWidth = xMax - colorswatchWidth - labelColumnWidth - legendGutter - legendPadding - labelGutter;

  // const activeCategories = useMemo(() => {
  //   return uniqueCategories.filter((category) => getIsActiveCategory(category, progress));
  // }, [progress]);

  const activeIndex = useMemo(() => Math.floor(getProgressBetweenPoints(progress, 0.2, 0.8) * gridHeight * gridWidth), [progress]);

  const dotMatrices = groupedData.map((group) => {
    const keyName = group.groupName.toLowerCase().split(' ').join('-');
    return <div key={`dot-matrix-container-${keyName}`} className='basis-1/2' style={{height: yMax}}>
      <h4 className="mb-2 text-sm">{group.groupName}</h4>
      <ParentSize>
       {
        ({width, height}) => <DotMatrix 
          data={group.data}
          total={totalCount}
          width={width}
          height={height}
          gridWidth={gridWidth}
          gridHeight={gridHeight}
          colorScale={colorScale}
          activeIndex={activeIndex}
          accessor="count"
          categoryAccessor="category"
        />
       }
      </ParentSize>
    </div>
  });
  return (
    <>
      <div className="flex basis-3/4 items-start">
        {dotMatrices}
      </div>
      <LegendOrdinal direction="column" scale={colorScale}  labelFormat={(label) => `${label}`}>
        {(labels) => (
          <div className="mt-6">
            <div style={{ fontSize: `16px`, paddingLeft: `${legendPadding}px` }} className="basis-1/4 pr-16">
              {labels.map((label, i) => (
                    <div
                      key={`legend-label-${label.text}`} 
                      className='px-2 my-2'
                      style={{ 
                        boxSizing: 'border-box', 
                        borderLeft: `12px solid ${label.value}`,
                        transition: 'background-color .5s'
                      }}
                    >
                      {label.text}
                    </div>

              ))}
            </div>
          </div>
        )}
      </LegendOrdinal>
    </>
  );
}

export default ExampleDotMatrix;