import { Group } from '@visx/group';
import { scaleLinear, scaleBand, scaleOrdinal } from '@visx/scale';
import crimeJailPopulation from '../data/crime-jail-population.json';
import React, { useMemo } from 'react';
import { animated, useTransition } from '@react-spring/web';
import { getProgressBetweenPoints } from '../lib';

const dataset = crimeJailPopulation;

// const max = (data, key) => Math.max(...(data).map(datum => datum[key]));
// const min = (data, key) => Math.min(...(data).map(datum => datum[key]));

const keys = Object.keys(dataset[0]).filter((key) => key !== 'Year');

const indicatorScale = scaleBand({
  domain: keys,
  padding: 0.2
});

const shootingsScale = scaleLinear({
  domain: [0, 8000],
});

const homicidesScale = scaleLinear({
  domain: [0, 8000],
});

const dailyJailPopulationScale = scaleLinear({
  domain: [0, 20000],
});

const mayorScale = scaleOrdinal({
  domain: [1993, 1994, 1995, 1996, 1997, 1998, 1999, 2000, 2001, 2002, 2003, 2004, 2005, 2006, 2007, 2008, 2009, 2010, 2011, 2012, 2013, 2014, 2015, 2016, 2017, 2018, 2019, 2020],
  range: ['Giuliani', 'Giuliani', 'Giuliani', 'Giuliani', 'Giuliani', 'Giuliani', 'Giuliani', 'Giuliani', 'Giuliani', 'Bloomberg', 'Bloomberg', 'Bloomberg','Bloomberg','Bloomberg','Bloomberg','Bloomberg','Bloomberg','Bloomberg','Bloomberg','Bloomberg','Bloomberg','DiBlasio','DiBlasio','DiBlasio','DiBlasio','DiBlasio','DiBlasio','DiBlasio','DiBlasio']
});

const gradientScale = scaleOrdinal({
  domain: ['Shooting Incidents', 'Homicides', 'Jail ADP'],
  range: ['#shootingsGradient', '#homicidesGradient', '#jailPopGradient'],
});

const scaleScale = scaleOrdinal({
  domain: ['Shooting Incidents', 'Homicides', 'Jail ADP'],
  range: [shootingsScale, homicidesScale, dailyJailPopulationScale],
});

const defaultMargin = {top: 80, left: 20, bottom: 0, right: 20};

const ShootingsHomicidesIncarcerationChart = ({progress, prescroll, width, height, margin = defaultMargin}) => {
  const index = useMemo(() => Math.round(getProgressBetweenPoints(progress, prescroll, 0.8) * (dataset.length - 1)), [progress, prescroll]);
  const data = Object.keys(dataset[0]).map((key) => {
    const ret = {};
    ret.key = key;
    ret.val = dataset[index][key];
    return ret;
  }).filter((item) => item.key !== 'Year');

  const xMax = width - margin.left - margin.right;
  const yMax = height - margin.top - margin.bottom;

  const transitions = useTransition(data, {
    keys: (item) => item.key,
    enter: (item) => {
      const scale = scaleScale(item.key);
      return {
          x: indicatorScale(item.key),
          y: scale(item.val),
          textY: Number(scale(item.val)) - 36,
        }
    },
    // keys: (item) => Object.keys(item).filter(key => key !== 'Year'),
    update: (item) => {
      const scale = scaleScale(item.key);
      return {
          x: indicatorScale(item.key),
          y: scale(item.val),
          textY: Number(scale(item.val)) - 36,
        }
    },
  });

  indicatorScale.range([0, xMax]);
  shootingsScale.range([yMax, 0]);
  homicidesScale.range([yMax, 0]);
  dailyJailPopulationScale.rangeRound([yMax, 0]);

  const backgrounds = data.map((item) => {
    const scale = scaleScale(item.key);
    return (
      <React.Fragment key={`bg-item-${item.key}`}>
        <rect 
          x={indicatorScale(item.key)}
          y={0}
          height={yMax}
          width={indicatorScale.bandwidth()}
          fill={`url(${gradientScale(item.key)})`}
        />
        <text
          x={indicatorScale(item.key) + indicatorScale.bandwidth()}
          y={-4}
          transform="translate(-40)"
          fill="#666"
          fontSize={12}
        >
          {scale.domain()[1].toLocaleString('en-US')}
        </text>
      </React.Fragment>
    );
  })

  return (xMax < 10) ? null : (
    <div className="relative">
    <div className="text-center mt-4 text-sm absolute left-1/2 -translate-x-1/2 py-1 w-64 bg-black">
        <span className="text-white">{mayorScale(dataset[index].Year).toUpperCase()} | {dataset[index].Year}</span>
    </div>
    <svg width={width} height={height}>
      <defs>
        <linearGradient id="homicidesGradient" gradientTransform="rotate(90)" spreadMethod="repeat">
          <stop offset="0%" stopColor="#E1DC34"></stop>
          <stop offset="20%" stopColor="#848DAE"></stop>
          <stop ofset="66%" stopColor="#848DAE"></stop>
          <stop offset="99%" stopColor="#DA2D59"></stop>
        </linearGradient>
        <linearGradient id="shootingsGradient" gradientTransform="rotate(90)" spreadMethod="repeat">
          <stop offset="0%" stopColor="#DA2D59"></stop>
          <stop offset="20%" stopColor="#E1DC34"></stop>
          <stop ofset="66%" stopColor="#E1DC34"></stop>
          <stop offset="99%" stopColor="#848DAE"></stop>
        </linearGradient>
        <linearGradient id="jailPopGradient" gradientTransform="rotate(90)" spreadMethod="repeat">
          <stop offset="0%" stopColor="#848DAE"></stop>
          <stop offset="20%" stopColor="#F06342"></stop>
          <stop ofset="66%" stopColor="#F06342"></stop>
          <stop offset="99%" stopColor="#E1DC34"></stop>
        </linearGradient>
      </defs>
      <Group
        left={margin.left}
        top={margin.top}
      >
        { backgrounds }
        {
          transitions(({x, y, textY, key}, item) => {
            return <React.Fragment key={`bar-${key}-${data.Year}`}>
            <animated.rect 
              x={x}
              width={indicatorScale.bandwidth() * .75}
              y={y}
              height={3}
              fill='#000'
            />
            <animated.text
              x={indicatorScale(item.key)}
              y={textY}
              dy={16}
            >
              <tspan fontSize={12}>
                {item.key}
              </tspan>
              <tspan dy={15} fontSize={14} fontWeight={600} x={indicatorScale(item.key)}>
                {item.val.toLocaleString("en-US")}
              </tspan>
            </animated.text>
            </React.Fragment>
          })
        }
      </Group>
    </svg>
    </div>
  );
}

export default ShootingsHomicidesIncarcerationChart;