const CustomTick = (props, child) => {
  return props.props.ticks.map((tick, i) => {
    const valueSplit = tick.formattedValue.split("A city of");
    return <text
      fontSize={props.props.tickLabelProps[i].fontSize}
      key={`custom-tick-${i}`}
      x={tick.to.x}
      y={tick.to.y - props.props.tickLabelProps[i].fontSize - 4 }
      textAnchor={props.props.tickLabelProps[i].textAnchor}
    >
      <tspan 
        x={tick.to.x} 
        dy={`${props.props.tickLabelProps[i].fontSize * 1.2}px`}
      >
        {valueSplit[0].toUpperCase()}
      </tspan>
      <tspan 
        x={tick.to.x} 
        dy={`${props.props.tickLabelProps[i].fontSize * 1.2}px`}
        fillOpacity={0.5}
        fontSize={props.props.tickLabelProps[i].fontSize * 0.85}
      >
        {`A city of ${valueSplit[1]}`}
      </tspan>
    </text>})
}

export default CustomTick;