import React, {useState, useMemo, useEffect} from 'react';
import dataset from '../data/social-factors-community-districts.json';
import { ParentSize } from '@visx/responsive';
import SocialFactorsMapMap from './SocialFactorsMapMap';
import { scaleLinear } from '@visx/scale';
import { min, max, clamp } from '../lib';

const keys = Object.keys(dataset[0]).filter((key) => key !== 'Community District');

const MemoizedMap = React.memo(SocialFactorsMapMap);

// getters
const getterByKey = (key) => (datum) => datum[key]; 
const getCommunityDistrict = getterByKey("Community District");

const SocialFactorsMap = ({progress, factorRefs}) => {

  const [factorIndex, setFactorIndex] = useState(0);

  const factor = useMemo(() => keys[factorIndex], [factorIndex]);

  useEffect(() => {
    const newIndex = clamp(Math.round(progress * keys.length), 0, keys.length - 1);
    if(newIndex !== factorIndex){
      setFactorIndex(newIndex);
    }
  }, [progress, factorIndex]);

  const factorRange = useMemo(() => dataset.map(getterByKey(factor)), [factor]);

  const socialFactorColorScale = useMemo(() => {
    return scaleLinear({
      domain: [min(factorRange), max(factorRange)],
      range: ['#eee', '#e7466c']
    });
  }, [factorRange])

  const factorData = useMemo(() => {
    return dataset.reduce((acc, datum) => {
      acc[datum["Community District"]] = datum[factor];
      return acc;
    }, {});
  }, [factor])

  return ( 
    <div className="flex">
      <div className="basis-1/4 shrink-0">
        {
          keys.map((key, index) => (
            <div 
              className={`cursor-pointer border border-black text-sm p-4 m-2 ${key === factor ? "bg-black text-white" : "bg-white text-black"}`} 
              key={key.split(' ').join('-')}
              onClick={
                () => {
                  console.log(key);
                  console.log(factorRefs);
                  window.scrollTo({top: factorRefs[key].current.getBoundingClientRect().top + window.scrollY, behavior: 'smooth'})}
              }
            >
              {key}
            </div>
          ))
        }
      </div>
      <div className="basis-3/4">
        <ParentSize>
          {
            ({width, height}) => (
              <MemoizedMap width={width} height={height} socialFactorData={factorData} socialFactorColorScale={socialFactorColorScale} />
            )
          }
        </ParentSize>
      </div>
    </div>
  )
}

export default SocialFactorsMap;