import { Group } from '@visx/group';
import { scaleBand, scaleLinear, scaleOrdinal } from '@visx/scale';
import { AxisLeft, AxisBottom } from '@visx/axis';
import CustomTick from './CustomTick';
import { animated, useTransition } from '@react-spring/web';
import React, { useEffect, useState } from 'react';

const margin = { top: 20, right: 20, bottom: 40, left: 150 };

const dataset = [
  {
    name: 'New York',
    population: 8336817,
    homicideRate: 5.5,
  },
  {
    name: 'Los Angeles',
    population: 3979537,
    homicideRate: 8.8,
  },
  {
    name: 'Chicago',
    population: 2693959,
    homicideRate: 28.5,
  },
  {
    name: 'Houston',
    population: 2316797,
    homicideRate: 17.2,
  },
  {
    name: 'Phoenix',
    population: 1608139,
    homicideRate: 11.2
  },
  {
    name: 'Philadelphia',
    population: 1584064,
    homicideRate: 31.5
  },
  {
    name: 'San Antonio',
    population: 1434625,
    homicideRate: 11.2 
  },
  {
    name: 'San Diego',
    population: 1386932,
    homicideRate: 6.8,
  },
  {
    name: 'Dallas',
    population: 1304379,
    homicideRate: 16.9,
  },
  {
    name: 'San Jose',
    population: 1013240,
    homicideRate: 3.1,
  },
];

const getHomicideRate = (d) => Number(d.homicideRate);


const colorScale = scaleOrdinal({
  domain: dataset.map((data) => data.name),
  range: ['#cfa9be', '#e6466d', '#f47b56', '#9a9fbc', '#dde34c'],
})

const compose = (scale, accessor) => data => scale(accessor(data));
const barColor = compose(colorScale, (d) => d.name);

const HorizontalBarChart = ({width, height, progress}) => {
  const xMax = width - margin.left - margin.right;
  const yMax = height - margin.top - margin.bottom;

  const homicideRateScale = scaleLinear({
    range: [0, xMax],
    round: true,
    domain: [0, Math.max(...dataset.map(getHomicideRate))]
  });

  const cityScale = scaleBand({
    domain: dataset.map((data) => data.name),
    range: [0, yMax]
  })

  const axisScale = scaleBand({
    domain: dataset.map((data) => `${data.name} A city of ${data.population.toLocaleString("en-US")}`),
    range: [0, yMax]
  })

  const [data, setData] = useState([]);
  const [started, setStarted] = useState(false);

  const transitions = useTransition(data, {
    from: {width: xMax, opacity: 0, textPosition: 0},
    enter: (item) => {
      const width = homicideRateScale(getHomicideRate(item));
      const textWidth = getHomicideRate(item).toString().length * 10;
      return {width: width, textPosition: width - textWidth,  opacity: 1}
    },
    leave: {width: 0, opacity: 0, textPosition: 0},
    delay: 200,
  })

  const addDatum = (sourceData, nextIndex) => {
    const newData = sourceData.slice(0, nextIndex);
    setData(newData);
    if(nextIndex < sourceData.length){
      setTimeout(() => {
        addDatum(sourceData, nextIndex + 1)
      }, 200)
    }
  }

  useEffect(() => {
    if(progress > 0){
      if(!started){
        setStarted(true);
        addDatum(dataset, data.length + 1);
      }
    }
    else if(data.length > 0){
      setData([])
      setStarted(false);
    }
  }, [progress]);

  return (
    <svg width={width} height={height}>
      <Group left={margin.left} top={margin.top}>
        {/*{bars}*/}
        {transitions(({width, opacity, textPosition}, item) =>{
          return (<React.Fragment key={`bar-text-${item.name}`}>
            <animated.rect
              x={0}
              y={cityScale(item.name)}
              height={cityScale.bandwidth()}
              width={width}
              fill={barColor(item)}
              key={`bar-${item.key}`}
            />
            <animated.text 
              fill="#000" 
              fontSize={16}
              y={cityScale(item.name) + 20}
              x={textPosition}
              opacity={opacity}
              key={`text-${item.key}`}
            >
              {getHomicideRate(item)}
            </animated.text>
          </React.Fragment>)
        })}
        <AxisLeft
          scale={axisScale}
          stroke='#000000'
          tickStroke='#000000'
          hideAxisLine
          tickLabelProps={() => ({
            fill: '#000000',
            fontSize: 14,
            textAnchor: 'end',
            dy: '0.33em',
          })}
          ticksComponent={(props) => {
            return <CustomTick props={props}  />
          }}
        />
        <AxisBottom
          scale={homicideRateScale}
          top={yMax}
          numTicks={0}
          hideAxisLine
          label="Homicides per 100,000 residents"
        />
      </Group>
    </svg>
  );
}

export default HorizontalBarChart;