import { Mercator } from '@visx/geo';
import { geoCentroid } from 'd3-geo';
import precincts from '../data/precincts.json';
import React, { useCallback } from 'react';
import { useTooltip, Tooltip, defaultStyles } from '@visx/tooltip';

let tooltipTimeout;
const getPrecinct = (datum) => datum.hasOwnProperty('precinct') ? datum.precinct : null;

const ShootingsMapMap = ({width, height, shootingsInCurrentYear, shootingsColorScale}) => {
  const { showTooltip, hideTooltip, tooltipLeft, tooltipTop, tooltipData, tooltipOpen } = useTooltip();

  // sizing logic
  const centerX = width / 2;
  const centerY = height / 2;
  const fitWidth = 500;
  const fitHeight = 500;
  const initScale = 46000;
  const scale = (width/fitWidth) < (height/fitHeight) ? initScale * (width/fitWidth) : initScale * (height/fitHeight);
  const center = [-73.9758790753777, 40.6901959259204];

  const onMouseMove = useCallback((e, left, top, data) => {
  if(tooltipTimeout) {
    clearTimeout(tooltipTimeout);
  }
  showTooltip({
    tooltipTop: top,
    tooltipLeft: left,
    tooltipData: data,
  })
}, [showTooltip]);

const onMouseLeave = useCallback((e) => {
  tooltipTimeout = window.setTimeout(() => {
    hideTooltip();
  }, 300);
}, [hideTooltip]);

  return (
    <div className="mx-auto relative" style={{width: width, height: height}}>
      <svg width={width} height={height}>
        <Mercator
          data={precincts.features}
          translate={[centerX, centerY]}
          scale={scale}
          center={center}
        >
          {({features}) => 
            features.map(({feature, path, projection}, i) => {
              const coords = projection(geoCentroid(feature));
              const stylesObj = {
                fill: '#FFF',
                fontFamily: 'sans-serif',
                cursor: 'default',
              };
              const hasShootingsData = shootingsInCurrentYear.map(getPrecinct).indexOf(feature.properties.Precinct) > -1;
              const shootingsData = hasShootingsData ? shootingsInCurrentYear.find((item) => getPrecinct(item) === feature.properties.Precinct) : null;
              const data = {
                precinct: feature.properties.Precinct,
                shootingsData: shootingsData,
              };
              return (
                <React.Fragment key={`map-feature-${i}`}>
                  <path 
                    d={path || ''}
                    fill={data.shootingsData != null ? shootingsColorScale(data.shootingsData.shootings) : '#eee'}
                    stroke='#555'
                    strokeWidth={0.5}
                    onMouseLeave={onMouseLeave}
                    onMouseMove={(e) => onMouseMove(e, coords[0], coords[1], data.shootingsData)}
                  />
                </React.Fragment>
              )
            })
        }
        </Mercator>
      </svg>
    {tooltipOpen && tooltipData && (
      <Tooltip top={tooltipTop} left={tooltipLeft} style={defaultStyles}>
        <div>
          <small>{tooltipData.precinctName}<br />{tooltipData.shootings} shootings</small>
        </div>
      </Tooltip>
    )}
    </div>
  );
}

export default ShootingsMapMap;