import create from 'zustand';

const useStore = create((set) => {
  return {
    headerTextArray: ['Safety depends on ...'],
    setHeaderText: (text) => set((state) => ({headerTextArray: state.headerTextArray.concat([text])})),
    unsetHeaderText: (text) => set((state) => ({headerTextArray: state.headerTextArray.slice(0, state.headerTextArray.length - 1)})),
  }
});

export default useStore;